.dialogs{
    display: flex;
    padding: 30px;
    border-bottom-right-radius: 20px;
    justify-content: space-between;
}

.dialogMainContainer{
    background-color: white;
    margin: 20px;
    border-radius: 20px;
}


.dialogMainContainer input {
    background-color: wheat;
    border-radius: 30px ;
    height: 40px;
    padding: 10px;
    margin: 30px;
    display: block;

}

.messageBlockDiv{
    padding: 50px;
    min-width: 400px;
}

.dialogMainContainer button{
    margin: 10px;
    border-radius: 40px;
    padding: 10px;
    font-family: "Sitka Banner";
    font-size: 15px;
    width: 150px;
    background-color: lightgoldenrodyellow;
}


.dialogsItems{
    padding: 50px;
}

.dialogsItems .dialog{
    font-family: "Yu Gothic UI Semilight";
    border: 2px solid dimgray;
    padding: 10px;
    margin: 5px;
    border-radius: 10px;
}



.messageRight{
    text-align: right;
    max-width: 200px;
    font-family: "Yu Gothic UI Semilight";
    margin-top: 10px;
    margin-bottom: 14px;
}

.messageLeft{
    text-align: left;
    max-width: 200px;
    font-family: "Yu Gothic UI Semilight";
    margin-top: 10px;
    margin-bottom: 14px;


}

.dialogsImg img{
    margin: 5px;
    vertical-align:middle;
    max-width: 60px;
    border-radius: 30px;
}

.dialogsCenter {
    border-right: 2px solid gray;
}

.messageText{
    display: inline-block;
    border: 1px solid gray;
    border-radius: 30px;
    padding: 12px;
    background-color: lightgoldenrodyellow;
    vertical-align:middle;
}



